	font-weight: 600;
	font-size: 35px;
	line-height: 1;
	transition: all 0.2s ease-in-out;
	margin: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 4.5rem 2.5rem;
