.bytron-img {
	width: 50%;
	height: 50%;
	margin: auto;
	border-radius: 35px;
	box-shadow: -20px 20px 60px;
}

.img-examples {
	width: 40%;
	margin: 10px;
}

.bytron-link {
	color: lightskyblue;
	text-decoration: none !important;
	position: relative;
	display: inline-block;
}
.bytron-link:after {
	background: none repeat scroll 0 0 transparent;
	bottom: 0;
	content: '';
	display: block;
	height: 2px;
	left: 50%;
	position: absolute;
	background: #fff;
	transition: width 0.3s ease 0s, left 0.3s ease 0s;
	width: 0;
}
a:hover:after {
	width: 100%;
	left: 0;
}

.react-img {
	position: absolute;
	width: 20%;
	right: 75%;
	bottom: 20%;
}

.plane {
