.loader {
	width: 100%;
	height: 100%;
	position: fixed;
	background: rgb(2, 2, 27);
	animation: zoom-out 0.5s ease-out forwards;
	z-index: 999;
	animation-delay: 1.5s;
	display: flex;
	align-items: center;
	justify-content: center;
}

.bouncingBall {
	width: 140px;
	height: 140px;
	border-radius: 100%;
	background: rgb(161, 146, 146);
	animation: bounce 0.8s;
	transform: translateY(0px);
	animation-iteration-count: infinite;
	position: fixed;
	margin-top: 20rem;
}

@keyframes bounce {
	0% {
		top: 0;
		-webkit-animation-timing-function: ease-in;
	}
	40% {
	}
	50% {
		top: 140px;
		height: 140px;
		-webkit-animation-timing-function: ease-out;
	}
	55% {
		top: 160px;
		height: 120px;
		-webkit-animation-timing-function: ease-in;
	}
	65% {
		top: 120px;
		height: 140px;
		-webkit-animation-timing-function: ease-out;
	}
	95% {
		top: 0;
		-webkit-animation-timing-function: ease-in;
	}
	100% {
		top: 0;
		-webkit-animation-timing-function: ease-in;
	}
}

@keyframes zoom-out {
	0% {
		transform: scale(1, 1);
		border-radius: 0%;
	}
	10% {
		border-radius: 50%;
	}
	100% {
		transform: scale(0, 0);
		border-radius: 50%;
		visibility: hidden;
	}
}

