.pdfLink {
	position: relative;
	padding: 20px 30px;
	width: 12rem;
	height: 4rem;
	z-index: 2;
	font-size: 1.2rem;
	display: block;
	margin: 0 auto;
	color: white;
	display: flex;
	justify-content: center;
	align-items: center;
	text-decoration: none;
}

.pdfLink::before {
	content: '';
	border: 1px solid #000;
	z-index: 1;
	border-radius: 50%;
	display: block;
	margin: -4px 2px;
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	transform: rotate(-10deg);
	-webkit-transform: rotate(-10deg);
	-ms-transform: rotate(-10deg);
	transition: transform 2.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.pdfLink::after {
	content: '';
	border: 1px solid #000;
	z-index: 1;
	border-radius: 50%;
	display: block;
	margin: -4px 2px;
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
}

.pdfLink:hover::before {
	transform: rotate(-20deg);
	-webkit-transform: rotate(-20deg);
	-ms-transform: rotate(-20deg);
	transition: transform 0.3s;
}

.pdfLink:hover::after {
	transform: rotate(4deg);
	-webkit-transform: rotate(4deg);
	-ms-transform: rotate(4deg);
	transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.project-img2 {
	border-radius: 5rem;
	width: 20%;
	position: absolute;
	left: 20rem;
	top: 5rem;
}

.sea-hero {
	color: lightskyblue;
	text-decoration: none !important;
}

.slide-down-uni {
	width: 50%;
	top: 50%;
	position: relative !important;
	overflow: hidden;
	height: fit-content;
}

.slide-right {
	width: 50%;
	position: relative;
	overflow: hidden;
}

.slide-left {
	position: relative;
	overflow: hidden;
}

.slide-right h2 {
	animation: 2s slide-right 2s forwards;
	transform: translateX(-100%);
	font-weight: 400;
	font-family: 'Poppins', sans-serif;
	font-size: 2vw;
	color: black;
}

.slide-left h2 {
	animation: 4s slide-right 2s forwards;
	transform: translateX(100%);
	font-family: 'Poppins', sans-serif;
	font-weight: 400;
	font-size: 1vw;
	color: black;
}

.year {
	animation: 2s slide-down 2s forwards;
	transform: translateY(-100%);
	width: 600px;
	font-family: 'Poppins', sans-serif;
	float: left;
	font-size: 2vw;
	color: black;
	font-weight: 700 !important;
}

@keyframes slide-right {
	to {
		transform: translateX(0);
	}
}

@keyframes slide-down {
	to {
		transform: translateY(0);
	}
}

